import { betterAuth } from "better-auth";
import { drizzleAdapter } from "better-auth/adapters/drizzle";
import { getDatabase } from "../data/database";
import * as authSchema from "../data/auth-schema";
import type { Auth } from "better-auth";

// Store auth instance for runtime
let authInstance: Auth | null = null;

/**
 * Get or create Better Auth instance
 * This function should be called with the D1Database instance from Cloudflare Workers
 * Uses Cloudflare D1 database via drizzle-orm/d1
 */
export const getAuth = (d1: D1Database, secret: string, baseURL: string): Auth => {


    console.log("baseURL", baseURL);
    console.log("secret", secret);
    console.log("d1", d1);

    if (!authInstance) {
        const db = getDatabase(d1);

        authInstance = betterAuth({
            database: drizzleAdapter(db, {
                provider: "sqlite",
                schema: authSchema,
            }),
            secret,
            baseURL,
            emailAndPassword: {
                enabled: true,
            },
        });
    }

    return authInstance;
};

